const Plante = () => {

    function createData(plant, agro, edible, med, femme, rev) {
        return { plant, agro, edible, med, femme, rev };
    }

    const ancestr = [
        createData('Acacia mearnsii', 'Avec son taux de croissance rapide et sa capacit  fixer l\'azote atmosphrique, elle peut jouer un rle important dans la restauration des forts indignes. Toutefois, en raison de sa tendance  envahir d\'autres habitats, elle ne devrait tre utilise de cette manire que dans sa zone d\'origine. L\'arbre pousse mme sur des pentes avec des sols peu profonds ou pauvres en acide qui sont instables et ne supporteront pas les cultures agricoles. Il peut donc tre trs efficace pour prvenir l\'rosion des sols. Des plantations denses se sont avres efficaces pour prvenir l\'rosion, mme sur des pentes allant jusqu\' 50 degrs.L\'arbre a galement t plant comme brise-vent ou comme coupe-feu. Fixateur d\'azote efficace et bonne source d\'engrais vert, il peut ainsi restaurer et rgnrer les sols. Le bois est utilis pour les poteaux de maison, les poignes d\'outils, l\'bnisterie, la menuiserie, les parquets, le bois de construction et le bois d\'allumette.', '( )', 'L\'corce contient des quantits plus ou moins importantes de tanins et est astringente. Ils sont par exemple utiliss dans le traitement de la diarrhe et de la dysenterie, et peuvent galement tre utiles en cas d\'hmorragie interne. En application externe, souvent sous forme de bain, ils sont utiliss pour traiter les plaies et autres problmes de peau, les hmorrodes, la transpiration des pieds, certains problmes oculaires, les bains de bouche, etc.', '(X)', '(X)'),
        createData('Calliandra calothyrsus', 'La plante peut tre utilise pour rhabiliter les zones sujettes  l\'rosion, telles que les pentes et les rives des cours d\'eau, et pour rcuprer les terres puises par l\'agriculture, o elle domine facilement les mauvaises herbes indsirables. Elle est utilise comme arbre nourricier pour les arbres  bois partiellement tolrants  l\'ombre. Fixent l\'azote atmosphrique, ont une production leve de biomasse foliaire et de protines foliaires sur des sols peu fertiles. Grce  la litire et  la combinaison d\'un systme d\'enracinement latral profond et bien dvelopp, le sol et la productivit de la terre sont amliors. Il est compatible avec les cultures, avec des racines profondes et des racines fibreuses tendues. Elle s\'est rvle prometteuse en tant que plante de sous-bois dans les plantations o la transmission de la lumire est d\'environ 60 %. La plante convient aux limites des haies. Les fleurs constituent une riche source de nectar pour les abeilles. Le bois peut tre utilis pour de petits travaux de menuiserie, des manches d\'outils, des cadres, etc.  Les proprits de la pte et du papier sont satisfaisantes et adaptes  la fabrication de papier kraft. Le bois convient comme combustible de fumage pour la production de feuilles de caoutchouc fumes. ', '( )', '( )', '(X)', '(X)'),
        createData('Grevillea robusta', 'Excellente espce agroforestire car elle interfre peu avec les cultures. Elle est considre comme plus compatible avec les cultures des petites exploitations que la plupart des autres espces d\'arbres. Les feuilles sont utilises comme paillis. Le chne soyeux fournit d\'abondantes quantits de paillis de feuilles, qui peuvent s\'accumuler jusqu\' une profondeur de 30  40 cm. Cette couche paisse protge le sol et maintient sa temprature. La plante produit de petites quantits de gomme-rsine. Les feuilles donnent des teintures jaunes et vertes intenses. Le bois est utilis pour le lambris, la menuiserie, l\'bnisterie, etc. Il convient  la fabrication de pte  papier. C\'est une plante trs apprcie pour le bois de chauffage.', 'Fleurs et feuilles', '( )', '(X)', '(X)'),
        createData('Leucaena leucocephala', 'Il s\'agit d\'une plante  croissance rapide dote d\'un systme racinaire tendu et qui a t utilise pour la rcupration des terres, la prvention de l\'rosion des sols et comme plante d\'ombrage. Son systme racinaire agressif aide  briser les couches compactes du sous-sol, amliorant la pntration de l\'humidit dans le sol et diminuant le ruissellement de surface. Les feuilles finement divises se dcomposent rapidement, fournissant un apport rapide et  court terme de nutriments. L\'arbre a le potentiel de renouveler la fertilit du sol et pourrait tre particulirement important dans les cultures sur brlis, car il rduit considrablement la priode de jachre entre les cultures. Des colorants rouges, bruns et noirs sont extraits des gousses, des feuilles et de l\'corce. Les graines sches sont largement utilises pour l\'ornementation. Il est facile  travailler pour une grande varit de travaux de menuiserie et sche sans se fendre ni se fissurer. Le bois de sciage, les meubles et les parquets sont des utilisations de plus en plus populaires. Le bois est gnralement rduit en pulpe pour sa fibre, utilise pour fabriquer du papier. Le bois brle rgulirement avec peu de fume, peu d\'tincelles et produit moins de 1% de cendres.', 'Feuilles, graines, gousses et boutons floraux', 'Une dcoction de la racine et de l\'corce est abortive. Les gousses et les graines sont un bon tonique. Les graines grilles sont mollientes. Les graines ont un effet bnfique et sont administres le matin  jeun. Parfois utilises fraches, les graines torrfies sont plus couramment prescrites sous forme de poudre ou de gteaux sucrs dans le traitement de l\'ascaridiose. Les graines contiennent une huile grasse. Les feuilles donnent du tanin, de la quercitrine et des alcalodes.', '(X)', '( )'),
        createData('Albizia gummifera', 'L\'arbre est apprci pour l\'ombre qu\'il apporte aux cultures, et il est galement plant ou conserv pour la conservation et l\'amlioration des sols. Le bois est utilis pour la construction lgre, l\'ameublement, l\'bnisterie et divers outils. Il convient galement pour les parquets lgers, la menuiserie, les garnitures intrieures, les panneaux, les charpentes, les botes, les caisses, les sculptures, les placages drouls et tranchs, les contreplaqus, les panneaux durs et les panneaux de particules. Les grumes sont traditionnellement utilises pour la construction de canos. La pulpe de bois convient  la production de papier.', 'Gomme de l\'corce', 'Une infusion de l\'corce est utilise pour traiter la malaria et pour acclrer la parturition. L\'corce pile est utilise comme tabac  priser pour traiter les maux de tte. L\'corce est applique en externe en guise de nettoyant corporel pour traiter des affections telles que la gale et le psoriasis. Les racines et les feuilles sont purgatives. Elles sont utilises dans le traitement de la diarrhe et des troubles oculaires. Les racines sont trempes dans l\'eau pendant 10 minutes et le liquide est bu pour soulager la douleur cause par les entorses. Une dcoction de feuilles est utilise comme antitussif et pour traiter l\'asthme. Les feuilles sont appliques en externe sur les plaies et les fractures. Un extrait des gousses crases est bu pour traiter les maux d\'estomac. ', '(X)', '( )'),
        createData('Erythrina abyssinica', 'Des plantes peuvent tre cultives pour former une "clture vivante"  partir de matraques. L\'arbre est recommand pour la conservation des sols, la lutte contre l\'rosion et l\'utilisation comme engrais vert. Les graines sont trs apprcies localement pour la fabrication de bibelots et de colliers. L\'corce est parfois utilise comme teinture brune pour les textiles. Une teinture rouge est obtenue  partir de la sve. Le lige de l\'corce est utilis comme flotteur pour les filets de pche. Le bois est couramment utilis pour fabriquer des sculptures, des tabourets, des tambours, des mortiers, des ruches, des manches d\'outils, des freins et des flotteurs pour les filets de pche, et parfois aussi pour la construction de maisons.', '( )', 'L\'corce est utilise pour traiter un large ventail d\'affections, notamment les morsures de serpent, le paludisme, les maladies sexuellement transmissibles telles que la syphilis et la gonorrhe, l\'amibiase, l\'lphantiasis, la toux, les inflammations du foie, les maux d\'estomac, les coliques et la rougeole. La sve de l\'corce est bue comme anthelminthique. En usage externe, l\'corce grille et pulvrise est applique sur les brlures, les ulcres et les gonflements. Le liquide issu de l\'corce broye des tiges vertes est utilis pour soigner la conjonctivite. Les racines sont utilises pour traiter les ulcres gastroduodnaux, l\'pilepsie, la malaria, la blennorragie et la schistosomiase. Les fleurs piles sont utilises pour traiter la dysenterie. Une macration de la fleur est bue comme abortif et applique en externe pour traiter les maux d\'oreille. Les feuilles sont appliques en externe sur les plaies et les articulations douloureuses. Les extraits de fruits sont utiliss pour traiter l\'asthme et la mningite.', '(X)', '( )'),
        createData('Moringa oleifera', 'Les plantes peuvent tre cultives comme une haie informelle, offrant une protection contre le vent, de l\'ombre et un support pour les plantes grimpantes. Largement utilis pour les cltures vivantes et les haies dans de nombreuses rgions. Convient  la plantation dans les alles de culture et dans les jardins potagers. Les graines rduites en poudre sont utilises pour clarifier le jus de canne  sucre. Le tourteau rsultant de l\'extraction de l\'huile peut galement tre utilis pour la purification de l\'eau. L\'corce, lorsqu\'elle est battue, produit une fibre utilise pour fabriquer de petites cordes et des nattes. Le bois donne une teinture bleue. Le tourteau de presse qui reste aprs l\'extraction de l\'huile des graines peut tre utilis comme amendement du sol ou comme engrais. Les feuilles crases sont utilises pour nettoyer les casseroles, les poles et les murs. Le bois tendre et lger brle sans fume et constitue un bois de chauffage acceptable pour la cuisine.', 'Jeunes feuilles et pousses, jeunes plants tendres, fleurs, gousses, graines, racines', 'L\'arbre est une plante nutritive, diurtique, laxative, expectorante, qui augmente le flux de lait, contrle les infections bactriennes et est rubfiant lorsqu\'il est appliqu localement. Il contient un antibiotique puissant. Les racines et l\'corce sont utilises pour les problmes cardiaques et circulatoires, comme tonique et contre l\'inflammation. L\'corce est apptissante et digestive. L\'corce et la gomme sont utilises dans le traitement de la tuberculose et de la septicmie.', '( )', '(X)'),
        createData('Mangifera indica', 'Grce  sa couronne en forme de parapluie, le manguier offre de l\'ombre aux personnes et  leur btail ; il sert galement de coupe-feu. Le manguier est cultiv pour son fruit. Le fruit vert est galement utilis pour aromatiser les plats de poisson et de viande, comme d\'autres fruits acides. Les jeunes feuilles sont cuisines comme un lgume. Les feuilles de manguier amliorent la fertilit du sol lorsqu\'elles sont utilises comme paillis pour les cultures. Importante plante mellifre, scrtant de grandes quantits de nectar.  Le bois est utilis  de nombreuses fins, notamment pour la construction d\'intrieurs, les blocs de hachage de viande, les meubles, la menuiserie, les planchers, les botes, les caisses et la construction de bateaux. L\'corce est  l\'origine d\'une teinture brun-jaune utilise pour la soie.', 'Fruits et jeunes feuilles', 'Les feuilles calcines et pulvrises constituent un empltre pour liminer les verrues et agissent galement comme styptique. Les graines sont utilises pour pour traiter les rhumes et les toux tenaces, les diarrhes obstines et les hmorragies de la vsicule biliaire. L\'corce est astringente, homostatique et antirhumatismale. antirhumatismale.', '( )', '(X)'),
        createData('Carica papaya', 'Les jeunes feuilles sont utilises comme paillis. Les feuilles sches peuvent tre battues dans de l\'eau pour former un substitut de savon. ', 'Fruits, graines, fleurs mles, trs jeunes feuilles', 'Une infusion du jeune fruit vert rempli de latex est utilise comme vermifuge pour les enfants. Le jus du fruit est utilis pour traiter le diabte et l\'hypertension. Le jus du fruit est utilis pour dissoudre les verrues. Les feuilles et les fruits, en particulier les fruits non mrs, sont pris en interne pour traiter une srie de troubles digestifs, la diarrhe, l\'hypertension artrielle et les douleurs utrines. Les feuilles vertes sont cuites pour traiter le paludisme tertiaire et les troubles du transit intestinal chez les enfants. En externe, les feuilles sont appliques sur les plaies comme un pansement qui aide  acclrer le processus de gurison. Le fruit mr est un laxatif doux. L\'corce interne est utilise pour traiter les maux de dents. L\'corce est utilise pour traiter la diarrhe. La racine macre est utilise dans le traitement de la gonorrhe.', '( )', '(X)'),
    ];


    return (

        <><CardContent sx={{ maxWidth: 800 }}>
            <Typography component='div' className="sentence" sx={{ mt: 2 }}>Such destruction, whose victims and accomplices we are, breaks the continuity with our traditional livelihood and our ancestral knowledge on biological resources and agroforestry management<br /><br /></Typography>
        </CardContent>

            <Accordion defaultExpanded={true}>
                <AccordionSummary
                    expandIcon={<ExpandMoreIcon />}
                    aria-controls="panel1a-content"
                    id="panel1a-header"
                >
                    <Typography sx={{ fontSize: "11px !important", WebkitTextFillColor: "rgb(105, 105, 105) !important" }} className="fishChallenges">LIST OF TREES WITH DISTINCT ANCESTRAL KNOWLEDGE</Typography>
                </AccordionSummary>
                <AccordionDetails>
                    <TableContainer component={Paper} sx={{ maxHeight: 250, overflow: 'auto' }}>
                        <Table stickyHeader sx={{ minWidth: 1000, fontSize: 13.5, fontWeight: "900 !important", }} aria-label="sticky table">
                                <TableHead>
                                    <TableRow >
                                        <TableCell>Tree name and species</TableCell>
                                        <TableCell align="center">Agroforestry use</TableCell>
                                        <TableCell align="center">Edible uses</TableCell>
                                        <TableCell align="center">Medicinal use</TableCell>
                                        <TableCell align="center">Knowledge and management by women</TableCell>
                                        <TableCell align="center">Revenue</TableCell>
                                    </TableRow>
                                </TableHead>
                                <TableBody>
                            {ancestr.map((row) => (
                                <TableRow
                                    key={ancestr.plant}
                                    sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                                >
                                    <TableCell component="th" scope="row">
                                        {row.plant}
                                    </TableCell>
                                    <TableCell align="center">{row.agro}</TableCell>
                                    <TableCell align="center">{row.edible}</TableCell>
                                    <TableCell align="center">{row.med}</TableCell>
                                    <TableCell align="center">{row.femme}</TableCell>
                                    <TableCell align="center">{row.rev}</TableCell>
                                </TableRow>
                            ))}
                        </TableBody>
                    </Table>
                </TableContainer>
            </AccordionDetails>
        </Accordion >
        </>
    )
}


export default Plante;
